import { BpmnModel } from "./BpmnModel";
import { CatchEvent } from "./CatchEvent";

export class IntermediateCatchEvent extends CatchEvent {
    readonly ClrTypeID: string = "bpmn.IntermediateCatchEvent";

    public constructor(model: BpmnModel) {
        super(model);
    }

    NewInstance(): IntermediateCatchEvent {
        return new IntermediateCatchEvent(this.Model);
    }
}